import React from "react";
import {
  Grid,
  Box,
  Typography,
  Card,
  CardContent,
  CardMedia,
  Container,
} from "@mui/material";
import 'react-multi-carousel/'
// Define a type for the coupon item
type Coupon = {
  title: string;
  store: string;
  code: string;
  icon: string;
};

const Hero: React.FC = () => {
  // Coupon data with proper typing
  const coupons: Coupon[] = [
    {
      title: "Free delivery",
      store: "TECH SHOP",
      code: "12oOTjcF3z",
      icon: "fas fa-truck",
    },
    {
      title: "10% Off",
      store: "6Valley",
      code: "rtzxxBnBlr",
      icon: "fas fa-dollar-sign",
    },
    {
      title: "75% Off",
      store: "TECH SHOP",
      code: "rfhfx7xlCm",
      icon: "fas fa-dollar-sign",
    },
  ];

  // Categories data
  const categories: string[] = [
    "Network Components",
    "Laptop, Tabs & Notebooks",
    "Consumer electronics",
    "Gadgets",
    "Computer & Office",
    "Mobile Accessories",
    "Smartphone",
    "Wearable",
    "Camera Accessories",
    "TV & Home Appliance",
    "Audio",
  ];

  return (
    <Box className="mt-10">
      <Container maxWidth={"xl"} className="">
        {/* Left side navigation */}
        <Grid container className="bg-white py-5 px-5">
          <Grid item xs={3}>
            <Box className="p-4">
              <ul className="space-y-2">
                {categories.map((item, index) => (
                  <li key={index} className="flex justify-between items-center">
                    <span>{item}</span>
                    <i className="fas fa-chevron-right"></i>
                  </li>
                ))}
              </ul>
              <div className="mt-4 text-[#1455ac]">
                <a href="#">View all</a>
              </div>
            </Box>
          </Grid>
          <Grid item xs={9}>
            {/* Right side content */}
            <Box className="flex flex-col space-y-4">
              <Grid container spacing={2}>
                <Grid item xs={8}>
                  <Grid container spacing={3}>
                    <Grid item xs={12}>
                      <Carousel></Carousel>
                      <Card>
                        <CardMedia
                          component="img"
                          height="300"
                          image="https://placehold.co/600x300"
                          alt="Special Offer on mobile phones"
                          className="rounded-lg"
                        />
                      </Card>
                    </Grid>
                    <Grid item xs={6}>
                      <Card>
                        <CardMedia
                          component="img"
                          height="150"
                          image="https://placehold.co/300x150"
                          alt="Black Friday limited time offer"
                          className="rounded-lg"
                        />
                      </Card>
                    </Grid>
                    <Grid item xs={6}>
                      <Card>
                        <CardMedia
                          component="img"
                          height="150"
                          image="https://placehold.co/300x150"
                          alt="Black Friday sale on all items"
                          className="rounded-lg"
                        />
                      </Card>
                    </Grid>
                  </Grid>
                </Grid>

                <Grid item xs={4}>
                  {/* Coupons section */}
                  <Card className="p-4 rounded-lg shadow mt-5 bg-[#fafafa]">
                    <CardContent>
                      <Typography variant="h6" className="font-bold text-[#1455ac]">
                        Happy Club
                      </Typography>
                      <Typography variant="body2" className="mb-4">
                        Collect coupons from stores and apply to get special
                        discounts
                      </Typography>
                      <Box className="space-y-4">
                        {coupons.map((coupon, index) => (
                          <Box key={index} className="border p-2 rounded-lg">
                            <Typography className="font-bold">
                              {coupon.title} <i className={coupon.icon}></i>
                            </Typography>
                            <Typography>For {coupon.store}</Typography>
                            <Typography className="text-[#1455ac]">
                              Code: {coupon.code}
                            </Typography>
                          </Box>
                        ))}
                      </Box>
                    </CardContent>
                  </Card>
                </Grid>
              </Grid>
            </Box>
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
};

export default Hero;
